"use client";

import React, { useState } from "react";
import { generateCoverLetter } from "@/actions/coverletter";
import { Loader2, Send } from "lucide-react";
import MDEditor from "@uiw/react-md-editor";
import { Button } from "@/components/ui/button";
import { toast } from "sonner";

export default function AICoverLetterPage() {
  const [input, setInput] = useState({
    jobTitle: "",
    companyName: "",
    jobDescription: "",
  });
  const [coverLetter, setCoverLetter] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleGenerate = async () => {
    if (!input.jobTitle || !input.companyName || !input.jobDescription) {
      toast.error("Please fill all fields");
      return;
    }

    try {
      setIsLoading(true);
      const result = await generateCoverLetter(input);
      setCoverLetter(result.content || "No response received.");
    } catch (error) {
      console.error(error);
      toast.error("Failed to generate cover letter");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="container mx-auto py-10 px-4 space-y-6 max-w-3xl">
      <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-500 to-cyan-400 text-transparent bg-clip-text">
        âœ¨ AI Cover Letter Generator
      </h1>
      <p className="text-muted-foreground">
        Generate a professional, personalized cover letter powered by AI.
      </p>

      {/* Input Fields */}
      <div className="space-y-4">
        <input
          type="text"
          placeholder="Job Title (e.g. Software Engineer)"
          className="w-full p-3 border rounded-lg bg-background"
          value={input.jobTitle}
          onChange={(e) => setInput({ ...input, jobTitle: e.target.value })}
        />
        <input
          type="text"
          placeholder="Company Name"
          className="w-full p-3 border rounded-lg bg-background"
          value={input.companyName}
          onChange={(e) => setInput({ ...input, companyName: e.target.value })}
        />
        <textarea
          placeholder="Paste the Job Description here..."
          className="w-full p-3 h-40 border rounded-lg bg-background resize-none"
          value={input.jobDescription}
          onChange={(e) =>
            setInput({ ...input, jobDescription: e.target.value })
          }
        />
      </div>

      {/* Generate Button */}
      <div className="flex justify-end">
        <Button onClick={handleGenerate} disabled={isLoading}>
          {isLoading ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" /> Generating...
            </>
          ) : (
            <>
              <Send className="mr-2 h-4 w-4" /> Generate Cover Letter
            </>
          )}
        </Button>
      </div>

      {/* Markdown Preview */}
      {coverLetter && (
        <div
          data-color-mode="light"
          className="p-5 border rounded-lg shadow bg-white mt-6"
        >
          <h2 className="text-lg font-semibold mb-3 text-gray-700">
            ðŸ“„ Generated Cover Letter
          </h2>
          <MDEditor.Markdown
            source={coverLetter}
            style={{ whiteSpace: "pre-wrap", color: "black" }}
          />
        </div>
      )}
    </div>
  );
}
